$primaryColor: #4527a4;

// Services
.wp-block-b-blocks-services .bBlocksServices {
	display: grid;

	* {
		box-sizing: border-box;
	}

	.wp-block-b-blocks-service {
		height: 100%;
		max-width: 100%;
	}

	&.layout1 {
		.wp-block-b-blocks-service:hover {
			.bBlocksService {
				transform: translateY(0);
			}
		}
	}
}

// Service
.wp-block-b-blocks-service {
	.bBlocksService {
		height: 100%;
		position: relative;
		transition: all .5s ease;

		.serviceLink {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0px;
			bottom: 0px;
		}

		.bgLayer {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0px;
			bottom: 0px;
			opacity: 0;
			transition: all .5s ease;
			z-index: -1;
		}

		.icon {
			display: inline-block;
			line-height: initial;

			i {
				font-style: normal;
			}
		}

		a {
			text-decoration: none;
		}

		.description {
			margin-bottom: 15px;
		}

		.link {
			position: absolute;
			left: 50px;
			bottom: -23px;

			a {
				position: relative;
				display: inline-flex;
				width: 50px;
				height: 50px;
				align-items: center;
				justify-content: center;
				font-size: 20px;
				line-height: 50px;
				text-align: center;
				border-radius: 50%;
				transition: all .5s ease;
				z-index: 1;

				svg {
					display: flex;
					align-items: center;
					justify-content: center;
				}
			}
		}
	}

	&:hover {
		.bBlocksService {
			transform: translateY(-15px);
			background: none !important;

			.bgLayer {
				opacity: 1;
			}
		}
	}
}